@import "../../../colors/colors";

.slider-container {
  display: flex;
  vertical-align: middle;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  transition: background 2s linear;
  &:after {
    content:
            url('../../../assets/slider1.png')
            url('../../../assets/slider2.png')
            url('../../../assets/slider3.png');
    display: none;
  }
  &__first {
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%), url("../../../assets/slider1.png");
  }
  &__second {
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%), url("../../../assets/slider2.png");
  }
  &__third {
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%), url("../../../assets/slider3.png");
  }
  &__fourth {
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%), url("../../../assets/slider4.png");
  }
  &__step-left {
    position: relative;
    height: 100%;
    width: 10.52%;
    &:hover {
      background: $green;
      opacity: 0.2;
      cursor: url("../../../assets/pointer.svg"), pointer;
    }
  }
  &__step-right {
    position: relative;
    right: 0;
    height: 100%;
    width: 10.52%;
    margin-left: auto;
    &:hover {
      background: $green;
      opacity: 0.2;
      cursor: url("../../../assets/pointer.svg"), pointer;
    }
  }
  &__arrow {
    position:absolute;
    margin:auto;
    top:0;
    bottom:0;
    left:0;
    right:0;
  }
}

.slider-text {
margin-top: 216px;
  margin-left: 7%;
  width: 65%;
  color: white;
  &__title {
    font-weight: 500;
    font-size: 40px;
    margin: 0;
  }
  &__subtitle {
    font-weight: 300;
    font-size: 24px;
    margin-top: 3%;
  }
}

.slider-button {
  border-radius: 4px;
  width: 164px;
  height: 48px;
  color: $grey-light;
  letter-spacing: 0.5px;
  border: none;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  &__first {
    background: linear-gradient(90deg, #13493F 0%, #0C7B1B 100%);
  }
  &__second {
    background: linear-gradient(90deg, #132949 0%, #0C7B67 100%);
  }
  &__third {
    background: linear-gradient(90deg, #493013 0%, #7B0C3B 100%);
  }
  &__fourth {
    background: linear-gradient(90deg, #281349 0%, #720C7B 100%);
  }
}
.slider-dots {
  position: absolute;
  height: 8%;
  bottom: 0;
  width: 50%;
  text-align: center;
  &__item {
    border-radius: 50%;
    margin-right: 8px;
    width: 8px;
    height: 8px;
    margin-top: 13px;
    background-color: $grey-light;

    content: '';
    display: inline-block;
    &_active {
      background-color: $green;
    }
  }
}

@media (max-width: 1439px) {
  .slider-dots {
    width: 40%;
  }

}


